import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import PropTypes from 'prop-types';

import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  IconButton,
  Container,
  Hidden,
} from '@material-ui/core';

import useStyles from './Header.styles';

import { ReactComponent as Logo } from '../../logo.svg';

import { auth } from '../../firebase/firebase.utils';
const Header = ({ stylistData }) => {
  const classes = useStyles();
  const isAuthorized = !!stylistData;
  return (
    <div className={classes.root}>
      <AppBar position='static'>
        <Container>
          <Toolbar className={classes.toolbar}>
            <IconButton
              edge='start'
              className={classes.menuButton}
              color='inherit'
              aria-label='menu'
              to='/stylist-alpha'
              component={RouterLink}
            >
              <Logo fill='white' width='40' height='40' />
            </IconButton>
            <Typography variant='h6' className={classes.title}>
              StylistAlpha
            </Typography>
            {isAuthorized ? (
              <>
                <Hidden xsDown>
                  <div className={classes.navigation}>
                    <Button
                      color='inherit'
                      to='/stylist-alpha/stylist-list'
                      component={RouterLink}
                    >
                      
                    </Button>
                  </div>
                </Hidden>
                <div className={classes.signOut}>
                  <Hidden xsDown>
                    <Typography variant='subtitle1' className={classes.welcome}>
                      ,&nbsp;
                      {stylistData.stylistName}
                    </Typography>
                  </Hidden>
                  <Button onClick={() => auth.signOut()} color='inherit'>
                    
                  </Button>
                </div>
              </>
            ) : (
              <>
                <Button
                  color='inherit'
                  to='/stylist-alpha/signin'
                  component={RouterLink}
                  className={classes.mrLeftAuto}
                >
                  
                </Button>
                <Button
                  color='inherit'
                  to='/stylist-alpha/signup'
                  component={RouterLink}
                >
                  
                </Button>
              </>
            )}
          </Toolbar>
        </Container>
      </AppBar>
    </div>
  );
};

Header.propTypes = {
  currentUser: PropTypes.instanceOf(Object),
};

Header.defaultProps = {
  currentUser: null,
};

export default Header;
