import React, { useState } from 'react';
import moment from 'moment';

import { reCaptcha } from '../../firebase/firebase.utils';

import ReCAPTCHA from 'react-google-recaptcha';

import {
  Grid,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from '@material-ui/core';

import TextFieldWithError from '../TextFieldWithError/TextFieldWithError';

import { Formik } from 'formik';
import * as yup from 'yup';

import useStyles from './SessionRequestDialog.styles';

const SessionRequestDialog = ({
  isOpen,
  setIsOpen,
  error,
  isSending,
  sendSessionRequest,
  stylist: { id, services },
}) => {
  const [submitErrorMessage, setSubmitErrorMessage] = useState(
    error ? JSON.stringify(error).toString() : ''
  );
  const [reCaptchaValue, setReCaptchaValue] = useState('');

  const classes = useStyles();
  const handleBack = () => setIsOpen(false);
  const handleChangerReCaptcha = (value) => setReCaptchaValue(value);
  const handleSubmit = async (values, actions) => {
    if (reCaptchaValue) {
      const sessionRequest = { ...values };
      sendSessionRequest({ stylistId: id, sessionRequest });
      setIsOpen(false);
    } else {
      alert(' ReCAPTCHA');
    }
  };

  const initialValues = {
    clientName: '',
    clientPhoneNumber: '',
    clientSocialLink: '',
    dateTime: moment(Date.now()).format('YYYY-MM-DDTkk:mm'),
    service: '',
  };

  const validationSchema = yup.object().shape({
    clientName: yup.string().required(" ' !"),
    clientPhoneNumber: yup
      .number('   ')
      .positive('   ')
      .integer('   ')
      .required(" ' !"),
    clientSocialLink: yup.string(),
    dateTime: yup.date(" ' !"),
    service: yup.string().required(" ' !"),
  });
  return (
    <Dialog open={isOpen} onClose={handleBack} aria-labelledby='confirm-dialog'>
      <DialogTitle className={classes.title}>  </DialogTitle>
      <Formik
        initialValues={initialValues}
        onSubmit={handleSubmit}
        validationSchema={validationSchema}
      >
        {(props) => {
          const handleChange = (e) => {
            setSubmitErrorMessage('');
            return props.handleChange(e);
          };
          return (
            <form onSubmit={props.handleSubmit}>
              <DialogContent>
                <Grid container direction='column' spacing={2}>
                  <Grid item>
                    <TextFieldWithError
                      className={classes.formControl}
                      value={props.values.clientName}
                      variant='outlined'
                      onChange={handleChange}
                      onBlur={props.handleBlur}
                      label="'"
                      name='clientName'
                      disabled={isSending}
                      isVisibleError={
                        props.errors.clientName && props.touched.clientName
                      }
                      errorMessage={props.errors.clientName}
                    />
                  </Grid>
                  <Grid item>
                    <TextFieldWithError
                      className={classes.formControl}
                      value={props.values.clientPhoneNumber}
                      variant='outlined'
                      onChange={handleChange}
                      onBlur={props.handleBlur}
                      label=' '
                      name='clientPhoneNumber'
                      disabled={isSending}
                      isVisibleError={
                        props.errors.clientPhoneNumber &&
                        props.touched.clientPhoneNumber
                      }
                      errorMessage={props.errors.clientPhoneNumber}
                    />
                  </Grid>
                  <Grid item>
                    <FormControl
                      variant='outlined'
                      className={classes.formControl}
                    >
                      <InputLabel id='serviceSelectLabel'></InputLabel>
                      <Select
                        labelId='serviceSelectLabel'
                        id='serviceSelect'
                        name='service'
                        value={props.values.service}
                        onChange={handleChange}
                        label=''
                      >
                        {services.map((s) => (
                          <MenuItem key={s.id} value={s.id}>
                            {s.displayName}
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl>
                    {props.errors.service && props.touched.service ? (
                      <Typography color='error'>
                        {props.errors.service}
                      </Typography>
                    ) : null}
                  </Grid>
                  <Grid item>
                    <TextFieldWithError
                      className={classes.formControl}
                      value={props.values.dateTime}
                      type='datetime-local'
                      variant='outlined'
                      onChange={handleChange}
                      onBlur={props.handleBlur}
                      label='  '
                      name='dateTime'
                      disabled={isSending}
                      isVisibleError={
                        props.errors.dateTime && props.touched.dateTime
                      }
                      errorMessage={props.errors.dateTime}
                    />
                  </Grid>
                  <Grid item>
                    <TextFieldWithError
                      className={`${classes.formControl} ${classes.notRequired}`}
                      value={props.values.clientSocialLink}
                      variant='outlined'
                      onChange={handleChange}
                      onBlur={props.handleBlur}
                      label='  .'
                      name='clientSocialLink'
                      disabled={isSending}
                      isVisibleError={
                        props.errors.clientSocialLink &&
                        props.touched.clientSocialLink
                      }
                      errorMessage={props.errors.clientSocialLink}
                    />
                  </Grid>
                  <Grid item>
                    <ReCAPTCHA
                      sitekey={reCaptcha.clientKey}
                      onChange={handleChangerReCaptcha}
                    />
                  </Grid>
                  {submitErrorMessage && (
                    <Grid item>
                      <Typography color='error'>
                        {submitErrorMessage}
                      </Typography>
                    </Grid>
                  )}
                  <Grid item>
                    <Typography color='textSecondary'>
                           
                    </Typography>
                  </Grid>
                </Grid>
              </DialogContent>
              <DialogActions>
                <Grid
                  className={classes.buttonContainer}
                  container
                  justify='space-between'
                >
                  <Button
                    variant='contained'
                    onClick={handleBack}
                    color='default'
                  >
                    
                  </Button>
                  <Button
                    variant='contained'
                    color='secondary'
                    type='submit'
                    disabled={props.isSubmitting}
                  >
                    
                  </Button>
                </Grid>
              </DialogActions>
            </form>
          );
        }}
      </Formik>
    </Dialog>
  );
};

export default SessionRequestDialog;
